import { Observable } from 'rxjs/Rx';
import {Injectable, Inject} from '@angular/core';

export declare var io: SocketFactory;

export interface Socket {
  on(event: string, callback: (data: any) => void);
  emit(event: string, data: any);
}

@Injectable()
export class SocketIoInteractor {
  socket: Socket;

  constructor(private url: string, private defaultEventName: string) {
    // check that url and eventName are valid
    this.socket = io.connect(url);
  }

  public getEventStream(eventName: string = this.defaultEventName): Observable<{}> {
    return Observable.create(observer => {
      this.socket.on(eventName, (data) => {
        observer.next(data);
      });

      return { dispose: io.close };
    });
  }

  public sendMessage(message: any, eventName: string = this.defaultEventName) {
    this.socket.emit(eventName, message);
  }
}

export interface SocketFactory {
  connect(url: string): Socket;
  close();
}



