import { Injectable, EventEmitter } from '@angular/core';
import { MdSnackBar } from '@angular/material';

@Injectable()
export class StatusNotificationService {
  public statusUpdate: EventEmitter<boolean> = new EventEmitter();

  constructor(public snackBar: MdSnackBar) { }

  public setIsUpdating(value: boolean) {
    this.statusUpdate.emit(value);
  }

  public openSnackBar(message: string, action: string, duration: number = 5000) {
    this.snackBar.open(message, action, {
      duration: duration,
    });

    const that = this;

    setTimeout(() => that.snackBar.dismiss(), 5000);
  }
}



