import { Component, OnInit, ChangeDetectionStrategy } from '@angular/core';
import { select } from '@angular-redux/store';
import { Observable } from 'rxjs/Observable';

import { TableViewState } from '../../models/TableViewState';
import { RestInteractorService } from '../../services/rest-interactor.service';

@Component({
  selector: 'dashboard-stocks',
  templateUrl: './stockdashboard.component.html',
  styles: [],
  changeDetection: ChangeDetectionStrategy.OnPush,
  viewProviders: [],
  providers: [RestInteractorService]
})
export class StockdashboardComponent implements OnInit {
  @select('tableState') public tableState$: Observable<TableViewState>;

  constructor(private restInteractorService: RestInteractorService) { }

  ngOnInit() {
    this.restInteractorService.getDefaultListResults();
  }
}



