import { NgModule, NO_ERRORS_SCHEMA, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { Routes, RouterModule, Route } from '@angular/router';
import { AppRouteProvider } from '../../shared';
import { MdSnackBar, OVERLAY_PROVIDERS, Overlay, OverlayContainer, LiveAnnouncer } from '@angular/material';
import { StatusNotificationService } from '../AppModule/services/status-notification.service';
// import { OverlayPositionBuilder } from '@angular/material/typings/core/overlay/position/overlay-position-builder';
import { StockDashboardModule } from '../StockDashboardModule/stock-dashboard.module';

const appRoutes: Route[] = AppRouteProvider.appRoutes;

@NgModule({
  imports: [StockDashboardModule, RouterModule, RouterModule.forRoot(appRoutes)],
  schemas: [NO_ERRORS_SCHEMA, CUSTOM_ELEMENTS_SCHEMA],
  providers: [MdSnackBar, OVERLAY_PROVIDERS, Overlay, StatusNotificationService, OverlayContainer
    ,/* OverlayPositionBuilder,*/ LiveAnnouncer],
  exports: [RouterModule],
  declarations: []
})
export class AppRoutingModule { }



