import { Injectable } from '@angular/core';
import { FormInputDescriptor } from 'app/modules/form-tests/reactive-form/metadata/FormInputDescriptor';
import { Validator } from 'app/modules/form-tests/reactive-form/metadata/Validator';
import { Validators } from '@angular/forms';
import { NumberValidators } from 'app/modules/form-tests/reactive-form/NumberValidators';
import { FormGroupDto } from 'app/modules/form-tests/reactive-form/metadata/formGroup';
import { formElementDescriptor } from 'app/modules/form-tests/reactive-form/metadata/formElementDescriptor';
import { BooleanElementDescriptor } from 'app/modules/form-tests/reactive-form/metadata/BooleanElementDescriptor';
import { FormElementTypes } from 'app/modules/form-tests/reactive-form/metadata/FormElementTypes';
import { LabelPositionTypes } from 'app/modules/form-tests/reactive-form/metadata/LabelPositionTypes';
import { SelectElementDescriptor } from 'app/modules/form-tests/reactive-form/metadata/SelectElementDescriptor';
import { EmailValidators, UrlValidators } from 'app/modules/form-tests/reactive-form/EmailValidation';

@Injectable()
export class MetadataService {
  public getFormGroupData(): FormGroupDto {
    let validationMessages = {};
    let formGroupData: any = {};

    let descriptors = this.getFormDescriptors();

    descriptors.forEach((x: formElementDescriptor) => {
      validationMessages[x.formControlName] = {};
      let validators = [];

      if (x instanceof FormInputDescriptor) {
        x.validators.forEach(y => {
          validators.push(y.validator);
          validationMessages[x.formControlName][y.key] = y.message;
        });
      }

      formGroupData[x.formControlName] = ['', validators];
    });

    const group = new FormGroupDto(validationMessages, formGroupData, descriptors);
    return group;
  }

  private getFormDescriptors(): formElementDescriptor[] {
    const descriptors = [];

    descriptors.push(new FormInputDescriptor('datetest', 'date test', 'date', []));
    descriptors.push(new FormInputDescriptor('datetime-local', 'datetime-local', 'datetime-local', []));
    descriptors.push(new FormInputDescriptor('email', 'Email Address', 'text',
      [new Validator('email', EmailValidators.validate(), 'A valid email is required.')]));
    descriptors.push(new FormInputDescriptor('month', 'month', 'month', []));
    descriptors.push(new FormInputDescriptor('number', 'number', 'number', []));
    descriptors.push(new FormInputDescriptor('password', 'password', 'password', []));
    descriptors.push(new FormInputDescriptor('search', 'search', 'search', []));
    descriptors.push(new FormInputDescriptor('tel', 'tel', 'tel', []));
    descriptors.push(new FormInputDescriptor('time', 'time', 'time', []));
    descriptors.push(new FormInputDescriptor('url', 'URL', 'url',
      [new Validator('url', UrlValidators.validate(), 'A valid URL is required.')]));
    descriptors.push(new FormInputDescriptor('week', 'week', 'week', []));

    descriptors.push(new FormInputDescriptor('productName', 'Product Name', 'text',
      [new Validator('required', Validators.required, 'Product name is required.'),
      new Validator('minlength', Validators.minLength(3), 'Product name must be at least 3 characters.'),
      new Validator('maxlength', Validators.maxLength(50), 'Product name cannot exceed 50 characters.')
      ]));

    descriptors.push(new FormInputDescriptor('productCode', 'Product Code', 'text',
      [new Validator('required', Validators.required, 'Product code is required.')]));
    descriptors.push(new FormInputDescriptor('starRating', 'Star Rating (1-5)', 'number',
      [new Validator('range', NumberValidators.range(1, 5), 'Rate the product between 1 (lowest) and 5 (highest).')]));
    descriptors.push(new FormInputDescriptor('description', 'Description'));

    descriptors.push(new BooleanElementDescriptor('isHappy', 'Are you happy?', FormElementTypes.Checkbox, 'accent',
      LabelPositionTypes.After, 'Good for you!', 'Cheer up! You don\'t have much time left', undefined, false, false));

    descriptors.push(new BooleanElementDescriptor('isWealthy', 'Are you Wealthy?', FormElementTypes.SlideToggle, 'accent',
      LabelPositionTypes.After, 'It won\'t last!', 'Give up and start drinking', undefined, false, false));

    descriptors.push(new SelectElementDescriptor(
      'isNice', 'is Nice', FormElementTypes.Autocomplete, x => x ? x.key : '', x => x ? x.description : '', [
        { key: '1', description: 'Somewhat' },
        { key: '2', description: 'Not at all' }]));

    descriptors.push(new SelectElementDescriptor(
      'isGood', 'is Good', FormElementTypes.Select, x => x.key, x => x.description, [
        { key: '1', description: 'Somewhat' },
        { key: '2', description: 'Not at all' }]));

    descriptors.push(new SelectElementDescriptor(
      'isHealthy', 'is Healthy', FormElementTypes.Radio, x => x.key, x => x.description, [
        { key: '1', description: 'Somewhat' },
        { key: '2', description: 'Not at all' }]));

    return descriptors;
  }
}



