import { formElementDescriptor } from 'app/modules/form-tests/reactive-form/metadata/formElementDescriptor';
import { FormElementTypes } from 'app/modules/form-tests/reactive-form/metadata/FormElementTypes';
import { LabelPositionTypes } from 'app/modules/form-tests/reactive-form/metadata/LabelPositionTypes';

export class BooleanElementDescriptor extends formElementDescriptor {
  constructor(
    formControlName: string = '',
    Label: string = '',
    type: string = FormElementTypes.Checkbox,
    color: string = 'accent',
    public labelPositionTypes: string = LabelPositionTypes.After,
    public trueLabel = undefined,
    public falseLabel = undefined,
    public defaultValue = undefined,
    public required = false,
    public isDisabled = false) {
    super(formControlName, Label, type);
  }
}



