import { BrowserModule } from '@angular/platform-browser';
import { NgModule, ErrorHandler } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MaterialModule } from '@angular/material';
import 'hammerjs';

import { SharedModule } from '../../shared/shared.module';
import { StockDashboardModule } from '../StockDashboardModule/stock-dashboard.module';
import { NewsReaderModule } from '../NewsReaderModule/news.module';

import { AppRoutingModule } from './app-routing.module';
import { InMemoryWebApiModule } from 'angular-in-memory-web-api';
import { InMemoryStoryService } from '../../../api/in-memory-story.service';

import { AppComponent } from './app.component';
import { NavigationComponent } from '../AppModule/navigation/navigation.component';
import { StatusNotificationService } from '../AppModule/services/status-notification.service';
import { ImmutableService } from '../../shared/modelServices/ImmutableService';

import { NgReduxModule, NgRedux } from '@angular-redux/store';

import { store, IAppState, CustomErrorHandler } from '../../shared';
import { FormTestsModule } from '../form-tests/form-tests.module';
import { RxjsTestingComponent } from './rxjs-testing/rxjs-testing.component';

@NgModule({
  bootstrap: [AppComponent],
  declarations: [
    AppComponent, NavigationComponent, RxjsTestingComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    HttpModule,
    NgReduxModule,
    AppRoutingModule,
    BrowserAnimationsModule,
    // InMemoryWebApiModule.forRoot(InMemoryStoryService, { delay: 500 }),
    MaterialModule,
    SharedModule,
    StockDashboardModule,
    NewsReaderModule,
    FormTestsModule
  ],
  providers: [StatusNotificationService, ImmutableService, {
    provide: ErrorHandler, useClass: CustomErrorHandler
  }]
})
export class AppModule {
  constructor(ngRedux: NgRedux<IAppState>) {
    ngRedux.provideStore(store);
  }
}



