import { Observable } from 'rxjs/Rx';
import {Injectable, Inject} from '@angular/core';

@Injectable()
export class WebWorkerInteractor {
  worker: WebWorker;

  /*
    <script type="javascript/worker" id="ww1">...</script>
    contains the worker
  */
  static getFromInlineJS(id: string) {
    const js = document.getElementById(id).textContent;
    const blob = new Blob([js], { 'type': 'text\/plain' });
    return URL.createObjectURL(blob);
  }

  constructor(worker: string, isInline: boolean = false) {
    if (isInline) {
    const js = WebWorkerInteractor.getFromInlineJS(worker);
    this.worker = new Worker(js);
    } else {
      this.worker = new Worker(worker);
    }
  }

  public getWorkerMessageStream(): Observable<{}> {
    return Observable.create(observer => {
      this.worker.onmessage = (event) => {
        observer.next(event);
      };
      return () => this.worker.terminate();
    });
  }

  public postMessage(message: any) {
    this.worker.postMessage(message);
  }
}

interface WebWorker {
  onmessage: Function;
  onerror: Function;
  postMessage(message: any, asas?): void;
  terminate(): void;
}



