import { NgModule } from '@angular/core';
import { StockdashboardComponent } from './components/stockdashboard/stockdashboard.component';
import { CommonModule } from '@angular/common';
import { BrowserModule } from '@angular/platform-browser';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { MaterialModule } from '@angular/material';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

import { StockDashboardRoutingModule } from './stock-dashboard-routing.module';
import { TodoComponent } from './components/todo';
import { OverviewComponent } from './components/overview/overview.component';
import { VolatilityPanelComponent } from './components/volatility-panel/volatility-panel.component';
import { ChartViewerComponent } from './components/chart-viewer/chart-viewer.component';
import { StockViewChangerComponent } from './components/stock-view-changer/stock-view-changer.component';
import { StockTableComponent } from './components/stock-table/stock-table.component';
import { RestInteractorService } from './services/rest-interactor.service';
import { GoogleSheetsService, YahooDataService, StockAnalysisService } from '../../shared';
import { NgReduxModule } from '@angular-redux/store';
import { StatusNotificationService } from '../AppModule/services/status-notification.service';
import { NO_ERRORS_SCHEMA, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
// import { MdGridTile } from '@angular/material/typings/grid-list/grid-tile';

@NgModule({
  declarations: [
    StockdashboardComponent,
    TodoComponent,
    OverviewComponent,
    VolatilityPanelComponent,
    ChartViewerComponent,
    StockViewChangerComponent,
    StockTableComponent],
  imports: [
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    HttpModule,
    BrowserAnimationsModule,
    MaterialModule,
    CommonModule,
    StockDashboardRoutingModule,
    NgReduxModule
  ],
  schemas: [NO_ERRORS_SCHEMA, CUSTOM_ELEMENTS_SCHEMA],
  providers: [
    RestInteractorService,
    GoogleSheetsService,
    YahooDataService,
    StockAnalysisService,
    StatusNotificationService,
    // MdGridTile
  ],
  bootstrap: [StockdashboardComponent]
})
export class StockDashboardModule { }



