import { Injectable } from '@angular/core';
import * as _ from 'lodash';

import { AuthGuard } from '../security/AuthGuard';
import { RouterAndMenuItem } from './RouterAndMenuItem';

import { RxjsTestingComponent } from 'app/modules/AppModule/rxjs-testing/rxjs-testing.component';
import { StockdashboardComponent } from 'app/modules/StockDashboardModule/components/stockdashboard/stockdashboard.component';
import { NewsReaderComponent } from 'app/modules/NewsReaderModule/news-reader/news-reader.component';
import { ReactiveFormComponent } from 'app/modules/form-tests/reactive-form/reactive-form.component';

@Injectable()
export class AppRouteProvider {
  public static appRoutes: RouterAndMenuItem[] = [
    {
      name: 'Financial Dashboard',
      icon: 'trending_up',
      path: 'dashboard',
      component: StockdashboardComponent,
      isDefault: true
    },
    {
      path: '',
      component: RxjsTestingComponent
    },
    {
      name: 'News',
      icon: 'business',
      path: 'news',
      component: NewsReaderComponent
    },
    {
      component: ReactiveFormComponent,
      name: 'Reactive Forms',
      icon: 'assignment',
      path: 'reactiveforms'
    }
  ];

  public getMenuItems(): RouterAndMenuItem[] {
    const routeConfig: RouterAndMenuItem[] = _.filter(AppRouteProvider.appRoutes, x => x.path.length > 0);
    return routeConfig;
  }
}



