import { Component, OnInit, Input, ChangeDetectorRef, Output, EventEmitter, ChangeDetectionStrategy } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { FormInputDescriptor } from 'app/modules/form-tests/reactive-form/metadata/FormInputDescriptor';

@Component({
  selector: 'form-input',
  templateUrl: './input.component.html',
  styleUrls: ['./input.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class InputComponent {
  @Input() formInputDescriptor: FormInputDescriptor;
  @Input() parentFormGroup: FormGroup;
  @Input() displayMessage: { [key: string]: string } = {};
  @Output() onChange: EventEmitter<any> = new EventEmitter();

  model: any;

  changed() {
    this.onChange.emit(this.model);
    // this.cd.detectChanges();
  }

  constructor(private cd: ChangeDetectorRef) { }
}



