import { NgModule } from '@angular/core';
import { NewsReaderComponent } from './news-reader/news-reader.component';

import { CommonModule } from '@angular/common';
import { BrowserModule } from '@angular/platform-browser';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { MaterialModule } from '@angular/material';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

import { GoogleSheetsService, YahooDataService, StockAnalysisService } from '../../shared';
import { NgReduxModule } from '@angular-redux/store';
import { StatusNotificationService } from '../AppModule/services/status-notification.service';
import { NO_ERRORS_SCHEMA, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';

@NgModule({
  bootstrap: [NewsReaderComponent],
  declarations: [
    NewsReaderComponent
  ],
  entryComponents: [],
  imports: [
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    HttpModule,
    BrowserAnimationsModule,
    MaterialModule,
    CommonModule,
    NgReduxModule
  ],
  providers: [
    GoogleSheetsService,
    YahooDataService,
    StockAnalysisService,
    StatusNotificationService,
  ],
  schemas: [NO_ERRORS_SCHEMA, CUSTOM_ELEMENTS_SCHEMA]
})
export class NewsReaderModule { }



