import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Rx';

import * as _ from 'lodash';

@Injectable()
export class GoogleFinanceService {
  constructor(private http: Http) {}

  public getTickerData(masterListTypes: any[]): Observable<Response[]>  {
    const smallLists: string[][] = [];
    const arraySize = 100;

    const tickers: string [] = _.chain(masterListTypes)
      .map(x => x.TickerListItems)
      .flatMap()
      .map((x: any) => x.Ticker)
      .uniqBy(x => x)
      .sortBy(x => x)
      .value();

    while (tickers.length) {
      const sublist: string [] = tickers.splice(0, arraySize);
      smallLists.push(sublist);
    }

    const result: Observable<Response[]> = Observable.forkJoin<Response>(_.map(smallLists, (x: string[]) => this.get(x).retry(2)));
    return result;
  }

  public get(tickers: string[]): Observable<Response> {
    let commaDelimitedTickers: string = _.reduce(tickers, (result, x) => result + x + ',', '');
    commaDelimitedTickers = commaDelimitedTickers.replace(/,\s*$/, '');
    const url = `http://www.google.com/finance/info?infotype=infoquoteall&q=${commaDelimitedTickers}`;
    return this.http.get(url);
  };
}



