import { Component, OnInit } from '@angular/core';
import { Observable, Observer } from 'rxjs/Rx';

@Component({
  selector: 'dashboard-rxjs-testing',
  templateUrl: './rxjs-testing.component.html',
  styleUrls: ['./rxjs-testing.component.scss']
})
export class RxjsTestingComponent implements OnInit {
  element: any;
  numbers = [1, 5, 4, 3, 2, 5, 8];
  source: Observable<any>;
  subscription: any;

  constructor() {
    //this.create();
    // setTimeout(() => {
    //   // this.element = document.getElementById('circle');
    //   this.element = document.getElementById('button');
    //   console.log(this.element.style);
    //   this.fromEvent();
    // }, 3000);
  }

  ngOnInit() {
    // this.source.subscribe(new MyObserver());
    if (!this.source) {
      return;
    }

    this.subscription = this.source.subscribe(
      next => console.log(next),
      error => console.log(error),
      () => console.log('complete'));
  }

  fromEvent() {
    // this.source = Observable.fromEvent(document, 'mousemove').map((c: MouseEvent) => {
    //   return {
    //     x: c.clientX,
    //     y: c.clientY
    //   };
    // }).filter(c => c.x < 500);

    this.source = Observable.fromEvent(document, 'click').map((c: MouseEvent) => {
      return {
        x: c.clientX,
        y: c.clientY
      };
    }).filter(c => c.x < 500);

    this.subscription = this.source.subscribe(
      next => {
        console.log(next);
      },
      error => console.log(error),
      () => console.log('complete'));
  }

  create() {
    this.numbers = [1, 5, 4, 3, 2, 5, 8];
    // this.source = Observable.from(this.numbers);
    this.source = Observable.create((observer: Observer<number>) => {

      // for (const n of this.numbers) {
      //   observer.next(n);

      //   if (n > 5) {
      //     observer.error('error!!!!');
      //   }
      // }

      // observer.complete();

      let index = 0;
      const prod = () => {
        observer.next(this.numbers[index++]);

        if (index < this.numbers.length) {
          setTimeout(prod, 1000);
        } else {
          observer.complete();
        }
      };

      prod();

    }).map(x => x * 2).filter(x => x > 5);
  }

  unsubscribe() {
    if (this.subscription) {
      this.subscription.unsubscribe();
      console.log('unsubscribed');
    } else {
      console.log('you are not subscribed to anything');
    }
  }
}

class MyObserver implements Observer<number> {
  next(value) {
    console.log(value);
  }

  error(e) {
    console.error(`The following Error occured`, e);
  }

  complete() {
    console.log('Observation Complete');
  }
}



