import { Component, OnInit, Input, ChangeDetectionStrategy } from '@angular/core';
import { select } from '@angular-redux/store';
import { Observable } from 'rxjs/Observable';

import { VixModel } from '../../models';

@Component({
  selector: 'volatility-panel',
  templateUrl: './volatility-panel.component.html',
  styleUrls: ['./volatility-panel.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class VolatilityPanelComponent {
  @select('vixModel') vixModel$: Observable<VixModel>;
  private readonly threshold = 0.917;

  isVixOverThreshold(vixModel: VixModel): boolean {
    if (!vixModel) {
      return false;
    }
    return this.getVixRatio(vixModel) > this.threshold;
  }

  getVixRatio(vixModel: VixModel): number {
    if (!vixModel) {
      return -1;
    }

    return vixModel.vix / vixModel.vixThreeMonth;
  }
}



