import { CompanyInformation } from './CompanyInfo';
import { StockListHeader } from './MasterListItem';

export class GoogleSheetsAdapterMap {
  public static map: { [key: string]: Function; } = {};

  private static masterSheetAdapter(items): StockListHeader[] {
    const result: StockListHeader[] = [];
    if (items.length === 0) {
      return [];
    }

    for (let i = 1; i < items.length; i++) {
      const row = items[i];
      const item = {
        title: row[0],
        sheetId: row[1],
        adapter: row[2],
        isActive: row[3],
        range: row[4]
      };
      result.push(item);
    }

    return result;
  }

  private static commonAdapter(items): CompanyInformation[] {
    const result: CompanyInformation[] = [];
    if (items.length === 0) {
      return [];
    }

    for (let i = 0; i < items.length; i++) {
      const row = items[i];
      const item = {
        category: row[0],
        ticker: row[1],
        isActive: row[2],
        Name: row[3]
      };
      result.push(item);
    }

    return result;
  }

  constructor() {
    GoogleSheetsAdapterMap.map['masterSheetAdapter'] = GoogleSheetsAdapterMap.masterSheetAdapter;
    GoogleSheetsAdapterMap.map['commonAdapter'] = GoogleSheetsAdapterMap.commonAdapter;
  }
}



