import { Component, OnInit, Input, ChangeDetectionStrategy, ChangeDetectorRef, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'boolean-input',
  templateUrl: './boolean-input.component.html',
  styleUrls: ['./boolean-input.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class BooleanInputComponent implements OnInit {
  @Input() type;
  @Input() color;
  @Input() isDisabled;
  @Input() required;
  @Input() defaultValue;
  @Input() label;
  @Input() trueLabel;
  @Input() falseLabel;
  @Input() labelPosition;
  @Output() onChange: EventEmitter<any> = new EventEmitter();

  indeterminate: boolean;
  checked: boolean;

  constructor(private cd: ChangeDetectorRef) { }

  ngOnInit() {
    if (typeof this.defaultValue === 'undefined') {
      this.indeterminate = true;
      this.checked = undefined;
    } else {
      this.checked = this.defaultValue;
    }

    if (typeof this.color === 'undefined') {
      this.color = 'accent';
    }

    if (typeof this.labelPosition === 'undefined'
      || (this.labelPosition !== 'after' && this.labelPosition !== 'before')) {
      this.labelPosition = 'after';
    }
  }

  changed() {
    this.onChange.emit(this.checked);
    this.cd.markForCheck();
  }

  getLabel(): string {
    this.label = this.label ? this.label : '';

    if (typeof this.checked === 'undefined') {
      return this.label;
    } else if (this.checked) {
      return this.trueLabel ? this.trueLabel : this.label;
    } else {
      return this.falseLabel ? this.falseLabel : this.label;
    }
  }
}



