import { formElementDescriptor } from 'app/modules/form-tests/reactive-form/metadata/formElementDescriptor';
import { FormElementTypes } from 'app/modules/form-tests/reactive-form/metadata/FormElementTypes';

export class SelectElementDescriptor extends formElementDescriptor {
  constructor(
    formControlName: string = '',
    Label: string = '',
    type: string = FormElementTypes.Select,
    public getKey: Function,
    public getDescription: Function,
    public getList: any[]) {
    super(formControlName, Label, type);
  }
}



