import { NgModule, CUSTOM_ELEMENTS_SCHEMA, NO_ERRORS_SCHEMA } from '@angular/core';
import { RouterModule, Route } from '@angular/router';

import { AppRouteProvider } from 'app/shared';
import { ReactiveFormComponent } from 'app/modules/form-tests/reactive-form/reactive-form.component';
import { DialogResultExampleDialog } from 'app/modules/form-tests/reactive-form/DialogResultExampleDialog';
import { template } from 'app/modules/form-tests/moduleForSpec';
import { InputComponent } from 'app/modules/form-tests/input/input.component';
import { AutoSelectComponent } from 'app/modules/form-tests/auto-select/auto-select.component';
import { BooleanInputComponent } from 'app/modules/form-tests/boolean-input/boolean-input.component';
import { MetadataService } from 'app/modules/form-tests/reactive-form/services/metadata.service';
import { MdAutocomplete, MaterialModule } from '@angular/material';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

const appRoutes: Route[] = AppRouteProvider.appRoutes;

const moduleParams: any = Object.assign({}, template);

moduleParams.imports.push(RouterModule.forRoot(appRoutes));
moduleParams.bootstrap = [ReactiveFormComponent];
moduleParams.entryComponents = [DialogResultExampleDialog];

@NgModule({
  declarations: [
    ReactiveFormComponent,
    InputComponent,
    DialogResultExampleDialog,
    AutoSelectComponent,
    BooleanInputComponent],
  entryComponents: [],
    imports: [CommonModule, FormsModule, ReactiveFormsModule, MaterialModule, RouterModule, BrowserAnimationsModule],
    providers: [MetadataService, MdAutocomplete],
    schemas: [NO_ERRORS_SCHEMA, CUSTOM_ELEMENTS_SCHEMA]
})
export class FormTestsModule { }



