import { Component, ChangeDetectionStrategy } from '@angular/core';
import { select, NgRedux } from '@angular-redux/store';
import { Observable } from 'rxjs/Observable';

import { DecoratedYahooQuote, Actions, IAppState } from '../../../../shared';
import { TableViewState } from '../../models';

@Component({
  selector: 'stock-table',
  templateUrl: './stock-table.component.html',
  styleUrls: ['./stock-table.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class StockTableComponent {
  @select('tableState') public tableViewState$: Observable<TableViewState>;
  @select('groupedYahooResults') groupedYahooResults$: Observable<DecoratedYahooQuote[][]>;
  @select('sortBy') sortBy$: Observable<string>;

  constructor(private ngRedux: NgRedux<IAppState>) {}

  chooseChipColor(value: number, upper: number, bottom: number, isReversed: boolean = false): string {
    const top: string = isReversed ? 'primary' : 'warn';
    const middle = 'accent';
    const lower: string = isReversed ? 'warn' : 'primary';

    try {
      return value > upper ? top : (value <= upper && value >= bottom ? middle : lower);
    } catch (ex) {
      return '';
    }
  }

  sortByColumn(column: string) {
    this.ngRedux.dispatch({
      type: Actions.StockDashboardActions.TABLE_SORT_CHANGE,
      sortBy: column,
    });
  }

  rowClicked(result: any) {
    const win = window.open(`http://www.gurufocus.com/stock/${result.symbol}`, '_blank');
    win.focus();
  }
}



