import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Rx';
import { CompanyInformation } from './CompanyInfo';
import { GoogleSheetsAdapterMap } from './GoogleSheetsAdapterMap';
import { StockListHeader } from './MasterListItem';

declare var gapi: any;

@Injectable()
export class GoogleSheetsService {
  token: any = false;
  CLIENT_ID = '844875982113-om6hos1ao3obpsoqhae3eo6b6meueqro.apps.googleusercontent.com';
  SCOPES: string[] = ['https://www.googleapis.com/auth/spreadsheets.readonly'];
  adapters: GoogleSheetsAdapterMap = new GoogleSheetsAdapterMap();

  mainsheet: any = {
    sheetId: '1XEoflheZAvbtpyBBs5TIuf-E2DpLRmCTMW_A_tctKS4',
    range: 'Sheet1!A1:E',
    adapter: 'masterSheetAdapter'
  };

  constructor() {
    this.token = false;
  }

  public getMasterSheetData(): Observable<StockListHeader[]> {
    if (!this.token) {
      return Observable.empty<StockListHeader[]>();
    }

    const wrappedCallback = Observable.bindCallback<StockListHeader[]>(this.callSheetsApi);
    return wrappedCallback(this.mainsheet);
  }

  public getSheetData(sheetInfo): Observable<CompanyInformation[]> {
    if (!this.token) {
      return Observable.empty<CompanyInformation[]>();
    }

    const wrappedCallback = Observable.bindCallback<CompanyInformation[]>(this.callSheetsApi);
    return wrappedCallback(sheetInfo);
  }

  private callSheetsApi(sheetInfo, callback) {
    gapi.client.load('https://sheets.googleapis.com/$discovery/rest?version=v4')
      .then(function () {
        gapi.client.sheets.spreadsheets.values.get({
          spreadsheetId: sheetInfo.sheetId,
          range: sheetInfo.range,
        }).then(function (response) {
          const values = response.result.values;
          const func = GoogleSheetsAdapterMap.map[sheetInfo.adapter];
          const result = func(values);
          callback(result);
        }, function (response) {
          console.error(`Error: ${response.result.error.message}`);
        });
      });
  }

  private handleAuthResult(authResult, callback) {
    if (authResult) {
      this.token = authResult;
      callback(this.token);
    }
  }

  public checkAuth(immediate, callback) {
    gapi.auth.authorize({
      client_id: this.CLIENT_ID,
      scope: this.SCOPES.join(' '),
      immediate: !immediate
    }, authResult => this.handleAuthResult(authResult, callback));

    return false;
  }
}



