import { Component } from '@angular/core';
import { MdDialogRef } from '@angular/material';

@Component({
  selector: 'dialog-result-example-dialog',
  template: `
  <div style="color:white;">
    <h1 md-dialog-title>Dialog</h1>
     <dashboard-reactive-form></dashboard-reactive-form>

    <div md-dialog-content>What would you like to do?</div>
    <div md-dialog-actions>
      <button md-raised-button color="accent" (click)="save()">Save</button>
      <button md-raised-button color="warn" (click)="cancel()">Cancel</button>
    </div>
  </div>
  `,
})
export class DialogResultExampleDialog {
  result: any = {
    action: ''
  };
  constructor(public dialogRef: MdDialogRef<DialogResultExampleDialog>) { }

  save() {
    this.result.action = 'was saved';
    this.dialogRef.close(this.result);
  }

  cancel() {
    this.result.action = 'was cancelled';
    this.dialogRef.close(this.result);
  }
}


