import { YahooQuote } from '../dataServices/YahooDataService/YahooQuote';
import { Injectable, Inject } from '@angular/core';

export interface DecoratedYahooQuote extends YahooQuote {
  group: string;
  mood: any;
  tickerLabel: string;
  movingAverageMeaning: string;
  projectedEpsGrowth: number;
  regressionPotential: number;
  relativeVolume: number;
  dailyPercentChange: number;
  grahamBuffettRatio: number;
  fundamentalAggregateNumber: number;
  impact: number;
}

const defaultValue: number = -1;

@Injectable()
export class StockAnalysisService {
  public decorateQuote(yahooQuote: YahooQuote, category: string): DecoratedYahooQuote {
    yahooQuote.PriceBook = yahooQuote.PriceBook ? parseFloat(yahooQuote.PriceBook.toString()) : defaultValue;
    yahooQuote.PriceSales = yahooQuote.PriceSales ? parseFloat(yahooQuote.PriceSales.toString()) : defaultValue;
    yahooQuote.PERatio = yahooQuote.PERatio ? parseFloat(yahooQuote.PERatio.toString()) : defaultValue;
    yahooQuote.PEGRatio = yahooQuote.PEGRatio ? parseFloat(yahooQuote.PEGRatio.toString()) : defaultValue;
    yahooQuote.DividendYield = yahooQuote.DividendYield ? parseFloat(yahooQuote.DividendYield.toString()) : defaultValue;
    yahooQuote.DividendShare = yahooQuote.DividendShare ? parseFloat(yahooQuote.DividendShare.toString()) : defaultValue;
    yahooQuote.EarningsShare = yahooQuote.EarningsShare ? parseFloat(yahooQuote.EarningsShare.toString()) : defaultValue;

    const quote: DecoratedYahooQuote = JSON.parse(JSON.stringify(yahooQuote));
    quote.MarketCapitalization = !quote.MarketCapitalization ? -1 : quote.MarketCapitalization;
    quote.tickerLabel = this.buildTickerLabel(quote);
    quote.ExDividendDate = this.adjustDividendDate(quote.ExDividendDate);
    quote.projectedEpsGrowth = this.calculateProjectedEpsGrowth(quote);
    quote.regressionPotential = this.calculateRegressionPotential(quote);
    quote.relativeVolume = this.calculateRelativeVolume(quote);
    quote.mood = this.evaluateSeriesData(quote);
    quote.movingAverageMeaning = this.MovingAvgMeaning(quote);
    quote.dailyPercentChange = this.getDailyPercentChange(quote);
    quote.grahamBuffettRatio = this.getGrahamBuffettRatio(quote);
    quote.fundamentalAggregateNumber = this.evaluateFundamentalData(quote);
    quote.group = category;
    quote.impact = (quote.dailyPercentChange * quote.relativeVolume) / 100;
    return quote;
  }

  public calculateProjectedEpsGrowth(yahooQuote: YahooQuote): number {
    const tmp = yahooQuote.EPSEstimateNextYear / yahooQuote.EPSEstimateCurrentYear;
    const relativeVolume = +(((tmp) - 1) * 100).toFixed(0);
    return relativeVolume;
  }

  public calculateRegressionPotential(yahooQuote: YahooQuote): number {
    const relativeVolume = +(((yahooQuote.YearHigh / yahooQuote.LastTradePriceOnly) - 1) * 100).toFixed(0);
    return relativeVolume;
  }

  public calculateRelativeVolume(yahooQuote: YahooQuote): number {
    const relativeVolume = +((yahooQuote.Volume / yahooQuote.AverageDailyVolume) * 100).toFixed(0);
    return relativeVolume;
  }

  public buildTickerLabel(yahooQuote: YahooQuote): string {
    let value: string = yahooQuote.MarketCapitalization > 1000
      ? (yahooQuote.MarketCapitalization / 1000).toString() + 'B'
      : yahooQuote.MarketCapitalization.toString() + 'M';

    value = value === '-1M' ? '' : yahooQuote.MarketCapitalization.toString();

    const label = `${yahooQuote.Symbol} ${value}`;
    return label;
  }

  public adjustDividendDate(dateText): string {
    if (!dateText) {
      return dateText;
    }

    const date = new Date(dateText);
    const x = 3; // or whatever offset
    date.setMonth(date.getMonth() + x);
    const result: string = (date.getMonth() + 1) + '/' + date.getDate() + '/' + date.getFullYear();
    return result;
  }

  public MovingAvgMeaning(yahooQuote: YahooQuote): string {
    const value: string = (yahooQuote.OneyrTargetPrice
      ? '(' + Math.round(((yahooQuote.OneyrTargetPrice / yahooQuote.LastTradePriceOnly - 1) * 100))
      + '% Target:' + yahooQuote.OneyrTargetPrice + ', '
      : '(') + 'MA50:' + yahooQuote.PercentChangeFromFiftydayMovingAverage + ') '
      + this.evaluateSeriesData(yahooQuote).text;
    return value;
  }

  public getDailyPercentChange(yahooQuote: YahooQuote): number {
    return +(((yahooQuote.LastTradePriceOnly / yahooQuote.PreviousClose) - 1) * 100).toFixed(2);
  }

  public getGrahamBuffettRatio(yahooQuote: YahooQuote): number {
    const value = +((22.5 / (yahooQuote.PriceBook * yahooQuote.PERatio)) * 100).toFixed(2);
    return value;
  }

  public evaluateFundamentalData(yahooQuote: YahooQuote): number {
    yahooQuote.PriceSales = parseFloat(yahooQuote.PriceSales.toString());
    yahooQuote.EarningsShare = parseFloat(yahooQuote.EarningsShare.toString());
    yahooQuote.DividendShare = parseFloat(yahooQuote.DividendShare.toString());

    const result = +((yahooQuote.EarningsShare + yahooQuote.DividendShare) / yahooQuote.PriceSales).toFixed(2);
    return result;
  }

  public evaluateSeriesData(yahooQuote: YahooQuote): any {
    const emptyString = '';
    const labelInfo = '';
    const labelSuccess = 'primary';
    const labelWarning = 'accent';
    const labelDanger = 'warn';

    let mood = {
      text: emptyString,
      cssClass: emptyString
    };

    const isTargetPriceAboveCurrent = yahooQuote.LastTradePriceOnly < yahooQuote.OneyrTargetPrice;
    const isAboveMacd50 = yahooQuote.PercentChangeFromFiftydayMovingAverage
      && yahooQuote.PercentChangeFromFiftydayMovingAverage.toString().charAt(0) === '+';
    const isAboveMacd200 = yahooQuote.PercentChangeFromTwoHundreddayMovingAverage
      && yahooQuote.PercentChangeFromTwoHundreddayMovingAverage.toString().charAt(0) === '+';

    if (!yahooQuote.LastTradePriceOnly
      || !yahooQuote.PercentChangeFromFiftydayMovingAverage
      || !yahooQuote.PercentChangeFromTwoHundreddayMovingAverage) {
      return {
        text: emptyString,
        cssClass: labelInfo
      };
    }

    if (isTargetPriceAboveCurrent && isAboveMacd50 && isAboveMacd200) {
      mood = {
        text: 'Strong uptrend' + (yahooQuote.OneyrTargetPrice ? ': Expected to outperform.' : emptyString),
        cssClass: yahooQuote.OneyrTargetPrice ? labelSuccess : labelInfo
      };
    } else {
      if (!isTargetPriceAboveCurrent && isAboveMacd50 && isAboveMacd200) {
        mood = {
          text: 'Strong uptrend' + (yahooQuote.OneyrTargetPrice ? ': Expected to under-perform.' : emptyString),
          cssClass: yahooQuote.OneyrTargetPrice ? labelWarning : labelInfo
        };
      } else if (isTargetPriceAboveCurrent && isAboveMacd50 && !isAboveMacd200) {
        mood = {
          text: 'recovering from long-term fall' + (yahooQuote.OneyrTargetPrice ? ': Expected to continue recovery.' : emptyString),
          cssClass: yahooQuote.OneyrTargetPrice ? labelSuccess : labelInfo
        };
      } else {
        if (!isTargetPriceAboveCurrent && isAboveMacd50 && !isAboveMacd200) {
          mood = {
            text: 'recovering from long-term fall' + (yahooQuote.OneyrTargetPrice ? ': Expected to move back lower' : emptyString),
            cssClass: yahooQuote.OneyrTargetPrice ? labelDanger : labelInfo
          };
        } else if (isTargetPriceAboveCurrent && !isAboveMacd50 && isAboveMacd200) {
          mood = {
            text: 'near-term falling off after strong growth' + (yahooQuote.OneyrTargetPrice ? ': Expected to resume rally.' : emptyString),
            cssClass: yahooQuote.OneyrTargetPrice ? labelSuccess : labelInfo
          };
        } else if (!isTargetPriceAboveCurrent && !isAboveMacd50 && isAboveMacd200) {
          mood = {
            text: 'near-term falling off after strong growth' +
            (yahooQuote.OneyrTargetPrice ? ': Expected to continue moving lower.' : emptyString),
            cssClass: yahooQuote.OneyrTargetPrice ? labelDanger : labelInfo
          };
        } else if (isTargetPriceAboveCurrent && !isAboveMacd50 && !isAboveMacd200) {
          mood = {
            text: 'strong long-term downtrend' + (yahooQuote.OneyrTargetPrice ? ': Expected to begin recovery.' : emptyString),
            cssClass: yahooQuote.OneyrTargetPrice ? labelWarning : labelInfo
          };
        } else if (!isTargetPriceAboveCurrent && !isAboveMacd50 && !isAboveMacd200) {
          mood = {
            text: 'strong long-term downtrend' + (yahooQuote.OneyrTargetPrice ? ': Continued collapse expected.' : emptyString),
            cssClass: yahooQuote.OneyrTargetPrice ? labelDanger : labelInfo
          };
        }
      }
    }

    return mood;
  }
}



