import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Rx';

import * as _ from 'lodash';

import { YahooQuote } from './YahooQuote';
import { CompanyInformation } from '../GoogleSheetsService/CompanyInfo';

@Injectable()
export class YahooDataService {
  constructor(private http: Http) { }

  public getTickerData(companyInformation: CompanyInformation[], columns: string = '*'): Observable<YahooQuote[]> {
    const smallLists: string[][] = [];
    const arraySize = 100;

    const tickers: string[] = _.chain(companyInformation)
      .map(x => x.ticker)
      .uniqBy(x => x)
      // .sortBy(x => x)
      .value();

    while (tickers.length) {
      const sublist: string[] = tickers.splice(0, arraySize);
      smallLists.push(sublist);
    }

    const result: Observable<YahooQuote[]> = Observable.forkJoin<YahooQuote>(_.flatMap(smallLists, (x: string[]) => this.get(x, columns)));
    return result;
  }

  private get(tickers: string[], columns: string = '*'): Observable<YahooQuote[]> {
    let commaDelimitedTickers: string = _.reduce(tickers, (result, x) => result + x + ',', '');
    commaDelimitedTickers = commaDelimitedTickers.replace(/,\s*$/, '');

    // averageDailyVolume symbol name lastTradePriceOnly
    const query: string = encodeURIComponent(`select ${columns} from yahoo.finance.quotes where symbol in ('${commaDelimitedTickers}')`);
    const url = `http://query.yahooapis.com/v1/public/yql?q=${
      query}&format=json&diagnostics=false&env=store%3A%2F%2Fdatatables.org%2Falltableswithkeys`;

    return this.http.get(url).map((x: Response) => {
      const body = x['_body'];
      const yahooFinanceResult: YahooFinanceResult = JSON.parse(body);
      return yahooFinanceResult.query.results.quote;
    }).retryWhen(this.retryStrategy({ attempts: 3, delay: 1500 }));
  }

  retryStrategy({ attempts = 4, delay = 1000 } = {}) {
    return function (errors) {
      return errors
        .scan((acc, value) => {
          acc += 1;
          if (acc < attempts) {
            return acc;
          } else {
            throw new Error(value);
          }
        }, 0)
        .delay(delay);
    };
  }
}

export interface YahooFinanceResult {
  count: number;
  created: Date;
  lang: string;
  results: YahooQuote[];
  query: IQuery;
}

interface IQuery {
  results: IQuoteResult;
}

interface IQuoteResult {
  quote: YahooQuote[];
}



