import { Directive, AfterViewInit } from '@angular/core';

declare const componentHandler: any;

@Directive({
  selector: '[mdl]'
})
export class MdlDirective implements AfterViewInit {
  ngAfterViewInit() {
    componentHandler.upgradeAllRegistered();
  }
}



