import {Injectable, Inject} from '@angular/core';
import { Map } from 'immutable';

@Injectable()
export class ImmutableService {
  public setPropertyOnImmutable<T>(object: T, field: string, value: any): T {
    const immutableMap = Map(object);
    const alteredInstance = immutableMap.set(field, value);
    const result = alteredInstance.toJS() as T;
    return result;
  }
}



