import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { WebWorkerInteractor } from 'app/shared/WebWorkerInteractor';
import { KeyCodes } from 'app/shared/key_codes';
import { AuthModule } from 'app/shared/security/auth.module';
import { AuthGuard } from 'app/shared/security/AuthGuard';
import { AppRouteProvider } from 'app/shared/routing/AppRouteProvider';
import { LocalStorageService } from 'app/shared/modelServices/LocalStorage';
import { StockAnalysisService, DecoratedYahooQuote } from 'app/shared/modelServices/stockAnalysisService';
import { MdlDirective } from 'app/shared/directives/MaterialDesignLiteUpgradeElement';
import { YahooDataService } from 'app/shared/dataServices/yahooDataService/YahooDataService';
import { SocketIoInteractor } from 'app/shared/dataServices/webSockets/SocketIoInteractor';
import { ChannelService,
  SignalrWindow,
  ConnectionState,
   ChannelConfig, ChannelEvent } from 'app/shared/dataServices/signalR/signalRService';
import { GoogleFinanceService } from 'app/shared/dataServices/googleFinanceService/GoogleFinanceService';
import { CompanyInformation } from 'app/shared/dataServices/googleSheetsService/CompanyInfo';
import { GoogleSheetsAdapterMap } from 'app/shared/dataServices/googleSheetsService/GoogleSheetsAdapterMap';
import { GoogleSheetsService } from 'app/shared/dataServices/googleSheetsService/GoogleSheetsService';
import { StockListHeader } from 'app/shared/dataServices/googleSheetsService/MasterListItem';
import { CapitalizePipe } from './pipes/capitalize/capitalize.pipe';

@NgModule({
  imports: [
    CommonModule,
    AuthModule
  ],
  providers: [AppRouteProvider],
  exports: [CapitalizePipe, MdlDirective],
  declarations: [CapitalizePipe, MdlDirective]
})
export class SharedModule { }



