import { Component, OnInit, Input, EventEmitter, Output, ChangeDetectionStrategy } from '@angular/core';
import { select, NgRedux } from '@angular-redux/store';
import { Observable } from 'rxjs/Observable';

import { StockListHeader, Actions, IAppState } from '../../../../shared';
import { TableViewState } from '../../models';
import { ImmutableService } from '../../../../shared/modelServices/ImmutableService';

import { RestInteractorService } from '../../services/rest-interactor.service';

@Component({
  selector: 'stock-view-changer',
  templateUrl: './stock-view-changer.component.html',
  styles: [],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class StockViewChangerComponent implements OnInit {
  @select('stockListHeaders') public stockListHeaders$: Observable<StockListHeader>;
  @select('tableState') public tableViewState$: Observable<TableViewState>;

  selectedStockList: string;
  showTechnical: boolean;
  showTable: boolean;

  constructor(
    private ngRedux: NgRedux<IAppState>,
    private restInteractorService: RestInteractorService) { }

  ngOnInit() {
    this.tableViewState$.subscribe(x => {
      this.selectedStockList = x.selectedStockList;
      this.showTable = x.showTable;
      this.showTechnical = x.showTechnical;
    });
  }

  public onChange() {
    this.ngRedux.dispatch({
      type: Actions.StockDashboardActions.TABLE_VIEW_STATE_CHANGE,
      tableState: new TableViewState(this.selectedStockList, this.showTable, this.showTechnical),
    });
  }
}



