import * as _ from 'lodash';

import { Actions } from './actions';
import { IAppState } from './IAppState';
import { VixModel, TableViewState } from 'app/modules/StockDashboardModule/models';
import { DecoratedYahooQuote } from 'app/shared';
import { StockDashboardReducer } from 'app/modules/StockDashboardModule/StockDashboardReducer';
import { BaseReducer } from 'app/shared/redux/BaseReducer';

export class RootReducer extends BaseReducer {
  private static rootReducer: RootReducer = new RootReducer();
  private static stockDashboardReducerService: StockDashboardReducer = new StockDashboardReducer();

  private static readonly initialState: IAppState = {
    groupedYahooResults: undefined,
    vixModel: new VixModel(),
    stockListHeaders: [],
    selectedStockList: '',
    tableState: new TableViewState(),
    sortBy: '',
    tickers4Analysis: []
  };

  public static reducer(state = RootReducer.initialState, action): IAppState {
    const modifiedState = RootReducer.stockDashboardReducerService.reducer(state, action);
    if (modifiedState !== state) {
      return modifiedState;
    }

    switch (action.type) {
      default: return state;
    }
  }
}



